.horizontal-nav {
  .offcanvas-header {
    display: none;
    pointer-events: none;
  }
}

header {
  .navbar {
    .iq-nav-menu {
      >li {
        margin-right: 2.3125em;

        &:last-child {
          margin-right: 0;
        }
      }

      li {
        position: relative;

        a {
          .arrow-active {
            font-size: 0.75em;
            opacity: 1;
            display: inline-block;
          }

          .arrow-hover {
            font-size: 0.75em;
            opacity: 0;
            display: none;
          }

          &[aria-expanded="true"] {
            .arrow-active {
              opacity: 0;
              display: none;
            }

            .arrow-hover {
              opacity: 1;
              display: inline-block;
            }
          }
        }

      }

      &.navbar-nav {
        li {
          ul {
            &.sub-nav {
              border-radius: var(--bs-border-radius);
              li {
                a {
                  &.nav-link {
                    .menu-icon {
                      position: absolute;
                      left: auto;
                      right: 15px;
                      top: 50%;
                      transform: translateY(-50%);

                      .toggle-menu {
                        top: 50%;
                        transform: translateY(-50%);
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }

    }

    ul {
      li {
        i.right-icon {
          display: inline-block;
          font-weight: 700;
          font-size: 1em;
        }
      }
    }

    .horizontal-nav {
      padding-left: 5.75rem;
    }
  }

  .user-icons {
    height: 2.5rem;
    width: 2.5rem;
    background: var(--bs-body-bg);
  }

  .dropdown-user {
    .user-info {
      border-bottom: 1px solid var(--bs-border-color);
      padding: 1rem 1.25rem;

      img {
        width: 2.5rem;
        height: 2.5rem;
        object-fit: cover;
      }
    }
  }

  .dropdown-user-wrapper {
    .dropdown-user {
      .user-image {
        height: 40px;
        width: 40px;
        object-fit: cover;
        border-radius: 50%;
      }
    }

    .dropdown-user-menu {
      min-width: 370px;

      .dropdown-user-menu-image {
        height: 48px;
        width: 48px;
        object-fit: cover;
        border-radius: 50%;
      }

      .content {

        .dropdown-user-menu-contnet {
          word-break: break-all;
        }
      }

      ul{
        .user-fields{
          .user-droup-down-img {
            background-color: var(--bs-body-bg);
            height: 3rem;
            width: 3rem;
            border-radius: 50%;
            text-align: center;
            line-height: 3rem;
            flex-shrink: 0;
            i{
              font-size: 1rem;
              color: var(--bs-primary);
            }
          }
        }
      }
    }
  }
  .dropdown-notification-wrapper{
    .dropdown-notification-menu{
      min-width: 350px;
      
      .notifcation-fields{
        background-color: var(--bs-gray-500);
        padding: 0.938rem;

        &:nth-child(odd) {
          background-color: var(--bs-gray-900);
        }

        .notification-droup-down-img{
          width: 2.813rem;
          min-width: 2.813rem;
          height: 2.813rem;
          line-height: 2.813rem;
          object-fit: cover;
        }
        .notification-time{
          font-size: 0.625rem;
          font-weight: $font-weight-semibold;
          text-transform: uppercase;
          flex-shrink: 0;
        }
      }
      .nav-pills{
        .nav-item {
          .nav-link{
            padding: 0.25rem 1.563rem;
            background-color: var(--bs-gray-500);
            color: var(--bs-body-color);
            &.active{
              background-color: var(--bs-secondary);
              color: var(--bs-white);
            }
          }
        }
      } 
      .notification-link{
        color: var(--bs-secondary);
        font-size: 0.625rem;
        font-weight: $font-weight-bold;
        background-color: transparent;
        flex-shrink: 0;
      }
    }
  }

  .dropdown-language-wrapper {
    .dropdown-menu-language {
      min-width: 200px;
      .dropdown-item {

      }
    }
  }

  .mobile-offcanvas {
    .btn-close {
      &:focus {
        border: none;
        box-shadow: none;
      }
    }
  }
}

.iq-nav-menu .nav-item>.nav-link:hover,
.iq-nav-menu .nav-item>.nav-link:hover i {
  color: var(--bs-primary);
}

.line{
  border-right: 1px solid var(--bs-border-color);
}

.iq-nav-menu {
  li {
    &.active {
      &>a {
        color: var(--bs-primary);
      }
    }

    &>ul {
      li {
        padding: 0;
        margin: 0;
      }
    }
  }

  &>li {
    &>a {
      font-weight: 500;
      font-size: 1rem;
      color: var(--bs-body-color);
    }
  }

  a {
    position: relative;
    display: block;
    padding: 0.625rem 1.875rem;
    outline-width: 0;
    color: var(--bs-body-color);
    text-decoration: none;

    .menu-icon {
      vertical-align: middle;
      font-size: .875rem;
    }
  }

  .menu-arrow {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-transform: capitalize;
  }
}

.nav-link:hover,
.nav-link:focus {
  color: var(--bs-primary);

  &:focus-visible {
    box-shadow: none;
  }
}

.nav {

  //navbar button
  .navbar-toggler {
    border: none;
    padding: 0;
    outline: none;
    vertical-align: middle;

    .navbar-toggler-btn {
      width: 30px;
      height: 30px;
      display: block;
      position: relative;

      .navbar-toggler-icon {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 1.7rem;
        height: 2px;
        background-color: var(--bs-body-color);
        background-image: none;
        display: inline-block;

        &::after,
        &::before {
          content: "";
          position: absolute;
          left: 0;
          transition: all 0.2s;
          width: 1.7rem;
          height: 2px;
          background-color: var(--bs-body-color);
          display: inline-block;
        }

        &::after {
          top: 0.6rem;
        }

        &::before {
          top: -0.6rem;
        }

      }

    }

    &[aria-expanded="true"] {
      .navbar-toggler-btn {
        .navbar-toggler-icon {
          background-color: transparent;
  
          &::before {
            top: 0;
            transform: rotate(135deg);
          }
  
          &::after {
            top: 0;
            transform: rotate(-135deg);
          }
        }
      }
    }
  }
}

.toggle-menu {
  position: absolute;
  left: auto;
  right: 0;
  top: 0;
  background: var(--bs-primary);
  border-radius: 0;
  text-align: center;
  display: block;
  height: 1.5em;
  width: 1.5em;
  line-height: 1.5em;
  font-size: 1em;
  z-index: 9;
  color: var(--bs-white);
  transition: all 0.3s ease-in-out;
  display: none;
}

.right-panel {
  .nav-link {
    padding: 0;
  }
}

@media(max-width: 1919.98px) {
  header {
    .navbar {
      .horizontal-nav {
        padding-left: 3rem;
      }

      .iq-nav-menu {
        li {
          margin-right: 1.5rem;
        }
      }
    }
  }
}

@media (max-width: 1699.98px) {
  header {
    .navbar {
      .horizontal-nav {
        padding-left: 2rem;
      }

      .iq-nav-menu {
        li {
          margin-right: .7rem;
        }
      }

      ul {
        li {
          i.right-icon {
            font-size: .8em;
          }
        }
      }
    }
  }
}

@include media-breakpoint-down(xxl) {
  header {
    .navbar {
      .iq-nav-menu {
        li {
          margin-right: 0;
        }
      }
    }
    .right-panel {
      .header-search {
        .input-group {
          width: 7.875rem;
        }
      }
    }
  }
}

@include media-breakpoint-up(xl) {
  .iq-nav-menu {
    .mega-menu-item{
      width: 840px;
      min-width: 100%;
      left: 50%;
      transform: translateX(-50%);
      border-radius: var(--bs-border-radius);
    }
  }
}

@include media-breakpoint-down(xl) {
  header {
    .navbar {
      .horizontal-nav {
        padding-left: 1rem;
        padding-right: 1rem;
      }

      .iq-nav-menu {
        li {
          .nav-link {
            &.active {
              color: var(--bs-primary);
              font-weight: 500;
            }

            .right-icon {
              display: none;
            }
          }
        }

        >li {
          margin: 0 0 1rem;

          &:last-child {
            margin: 0;
          }

          >a {
            padding: 0;
            color: var(--bs-heading-color);
          }
        }

        .sub-nav {
          margin-top: .5rem;
          
          li {
            padding: 0;

            >a {

              &::before,
              &::after {
                content: none;
              }
            }
          }
        }
      }
    }
  }

  .line{
    border-right: none;
  }

  .navbar-toggler {
    display: block;
  }

  .horizontal-nav {
    .offcanvas-header {
      opacity: 1;
      display: flex;
      justify-content: space-between;
      align-items: center;
      pointer-events: auto;

      .navbar-brand {
        .logo-normal{
          img{
            max-height: 1.5rem;
            width: 5.625rem;
          }
        }
      }
    }
  }

  .horizontal-nav {
    &.mobile-offcanvas {
      background: var(--bs-body-bg);
      visibility: hidden;
      transform: translateX(-100%);
      border-radius: 0;
      display: flex;
      flex-direction: column;
      align-items: initial;
      justify-content: initial;
      position: fixed;
      flex-wrap: nowrap;
      top: 0;
      bottom: 0;
      left: 0;
      height: 100vh;
      width: 80%;
      z-index: 1200;
      overflow-x: hidden;
      transition: visibility 0.3s ease-in-out, transform 0.3s ease-in-out,
        -webkit-transform 0.3s ease-in-out;

      .sub-nav {
        li {
          padding: 0;

          >a::before,
          >a::after {
            content: none;
          }
        }
      }

      .iq-nav-menu {
        flex-grow: 1;
        overflow-y: auto;
        overflow-x: hidden;

        li {
          .sub-nav {
            a {
              padding: .5rem 0.9375rem;
              font-weight: $font-weight-normal;
              font-size: 0.875rem;

              &:hover,
              &.active {
                color: var(--bs-primary);
              }
            }
          }
        }
      }
    }

    &.mobile-offcanvas.show {
      visibility: visible;
      transform: translateX(0);
    }

    &.mobile-offcanvas .container,
    &.mobile-offcanvas .container-fluid {
      display: block;
    }

    .offcanvas-header {
      opacity: 1;
      display: flex;
      justify-content: space-between;
      align-items: center;
      pointer-events: auto;
      padding-bottom: 2em;
    }
  }

  .iq-navbar {
    .navbar-collapse {
      &:not(.offcanvas-collapse) {
        position: absolute;
        top: 96%;
        left: 0;
        width: 100%;
        flex-grow: unset;
        background: var(--bs-gray-900);
        z-index: 1081;
        box-shadow: $box-shadow-sm;
        transition: all 400ms ease;

        .navbar-nav {
          flex-direction: row;
          float: right;
          padding: 0 1rem;
          align-items: center;
        }
      }
    }
  }

  .toggle-menu {
    display: block;
  }

}

@include media-breakpoint-down(lg) {
  .iq-navbar {
    .navbar-collapse {
      &:not(.offcanvas-collapse) {
        top: 100%;
      }

      .navbar-nav.navbar-list {
        &:not(.offcanvas-collapse) {
          flex-direction: row;
          float: right;
        }
      }
    }
  }

  header {
    .navbar {
      .horizontal-nav {
        .container-fluid {
          padding: 0 1rem;
        }
      }
    }
  }
}

@include media-breakpoint-down(xl) {
  .iq-navbar {
    z-index: 889;
  }
}

@include media-breakpoint-up(xl) {
  .iq-nav-menu {
    li {
      &>ul {
        position: absolute;
        height: auto;
        top: 100%;
        width: 100%;
        min-width: 16rem;
        z-index: 999;
        left: auto;
        right: auto;
        padding: 0.9375rem 0;
        display: none;
        margin: 0;
        background: var(--bs-gray-900);
        box-shadow: 0 0.625rem 1.875rem rgb(0 0 0 / 10%);
        animation: menu-sub-dropdown-animation-fade-in 0.3s ease 1,
          menu-sub-dropdown-animation-move-up 0.3s ease 1;

        li {
          padding: 0;
          padding: 0 0.9375em;

          a {
            transition: all 0.4s ease-in-out;
            transition: all 0.4s ease-in-out;
            padding: 0.5rem 1.25rem 0.5rem 1.25rem;
            overflow: hidden;
            font-size: 0.875em;
            border-radius: 0;
            display: inline-block;
            width: 100%;
            line-height: 1.5;
            padding: 0.938em;
            color: var(--bs-body-color);
            position: relative;
          }
        }

        ul {
          box-shadow: 0rem 0.25rem 2rem 0rem rgb(0 0 0 / 10%);
          left: auto;
          right: 100%;
          top: 0;
          box-shadow: 0rem 0.25rem 2rem 0rem rgb(0 0 0 / 10%);
        }
      }

      &:hover {
        &>ul {
          display: block;
        }
      }

      &.active {
        &>a {
          &>i {
            color: var(--bs-primary);
          }
        }
      }
    }

    ul {
      border-left: 0;
    }

    &>li {
      &>a {
        font-size: 0.975rem;
        padding: 1.8rem var(--bs-navbar-nav-link-padding-x);
        line-height: 1.5rem;
        z-index: 9;
        text-decoration: none;
        text-transform: capitalize;
        font-weight: 600;
        letter-spacing: 0.0625em;
      }
    }
  }

  header {
    .iq-nav-menu {
      ul {
        li {
          a {
            &:hover {
              color: var(--bs-primary);
            }
          }
        }
      }
    }
  }

  .iq-product-menu-responsive {
    &.offcanvas-end {
      width: auto;
    }
  }

  .product-offcanvas {
    .modal-backdrop {
      &.show {
        opacity: 0;
      }
    }
  }
}

@include media-breakpoint-up(xxl) {
  .iq-nav-menu {
    li {
      >ul {
        ul {
          left: 100%;
          right: auto;
        }
      }
    }
  }
}

@include media-breakpoint-down(md) {
  .horizontal-nav {
    &.mobile-offcanvas {
      width: 90%;
    }
  }

  header {
    .dropdown-user-wrapper {
      .dropdown-user-menu {
        min-width: inherit;
        width: 280px;

        .dropdown-user-menu-image {
          height: 35px;
          width: 35px;
        }
      }
    }
    .dropdown-notification-wrapper{
      .dropdown-notification-menu{
        min-width: inherit;
        width: 280px;
      }
    }

  }

  .iq-navbar {
    .navbar-collapse {
      &:not(.offcanvas-collapse) {
        .navbar-nav {
          width: 100%;
        }
      }
    }
  }
}

@include media-breakpoint-down(sm) {
  header {
    .dropdown-language-wrapper {
      .dropdown-menu-language {
        min-width: 10rem;
      }
    }
  }
}

@media(max-width: 299.98px) {
  header {
    .dropdown-user-wrapper {
      .dropdown-user-menu {
        min-width: 100%;
        width: inherit;
      }
    }
    .dropdown-notification-wrapper{
      .dropdown-notification-menu {
        min-width: 100%;
        width: inherit;
      }
    }
  }
}
